import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { css } from "@emotion/react"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-privacy.css"
import Campaign from "../../components/campaign"
import "../../styles/main-archives.css"

export default function Articles() {
  return (
    <Layout>
      <div class="wrap">
        <section class="archives">
          <h2>サポート事例</h2>
          <div class="archives-inner">
            <div class="flex">
              <div class="flex-item">
                <figure>
                  <StaticImage
                    src="../../images/main-case-study/go-sharoshi/ph1.jpg"
                    alt="GO社会保険労務士法人様"
                    placeholder="blurred"
                  />
                </figure>
                <p class="date">
                  2021.7.21
                </p>
                <p class="title">
                  【IT環境 総合診断】
                  <br/>
                  GO社会保険労務士法人&nbsp;様
                </p>
                <p class="txt">
                  これで良いのか？とずっと不安でしたが、
                  総合的に診断して頂き、不安が消え自信が持てました。
                </p>
                <div class="archives-btn">
                  <Link to={`/case-study/kanno-sharoshi`}>記事を読む</Link>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}